
html,
.container {
  width: 100%;
}
body {
  margin: 0;
  padding: 0;
}
.container > div {
  width: 100%;
}
.container > div > .big {
  width: 100%;
  display: block;
}
.footer {
  margin-top: 4rem;
  text-align: center;
}
.footer > span {
  font-size: 0.28rem;
  color: #717985;
}
.footer > p {
  margin-top: 0.2rem;
  font-size: 0.36rem;
  color: #002241;
}
.layer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  background: rgba(0,0,0,0.5);
  width: 100vw;
  height: 100vh;
}
.btns {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 2;
  padding: 0.3rem 0.2rem;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.btns > .btn {
  background: #fff;
  text-align: center;
  width: 100%;
  height: 1.14rem;
  line-height: 1.14rem;
  border-radius: 0.24rem;
  margin-top: 0.2rem;
  font-size: 0.36rem;
  color: #ff3a1e;
}
.btns > .btn > a {
  color: #384359;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.slide-enter,
.slide-leave-to {
  -webkit-transform: translateY(3rem);
          transform: translateY(3rem);
}